@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500&display=swap');

html,
body {
  padding: 0;
  margin: 0;
  height: fit-content;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  font-family: 'Baloo 2', sans-serif !important;
}

div#wentilab-wave {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100px;
  margin: 0 auto;
  margin-top: 30px;
}
div#wentilab-wave .dot {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 8px;
  background: #004ebc;
  animation: wave 1.3s linear infinite;
}
div#wentilab-wave .dot:nth-child(2) {
  animation-delay: -1.1s;
}
div#wentilab-wave .dot:nth-child(3) {
  animation-delay: -0.9s;
}

@keyframes wave {
  0%,
  60%,
  100% {
    transform: initial;
  }
  30% {
    transform: translateY(-15px);
  }
}

.wentilab-floating-chat {
  z-index: 9999;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 70px;
  height: 70px;
  transform: translateY(70px);
  transition: all 250ms ease-out;
  border-radius: 50%;
  opacity: 0;
  background: #004ebc;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.wentilab-floating-chat.enter:hover {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
  opacity: 1;
}
.wentilab-floating-chat.enter {
  transform: translateY(0);
  opacity: 1;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.25);
}
.wentilab-floating-chat.expand {
  width: 350px;
  max-height: 500px;
  height: 500px;
  border-radius: 5px;
  cursor: auto;
  opacity: 1;
  background-color: white;
}

.wentilab-floating-chat .chat {
  display: flex;
  flex-direction: column;
  position: absolute;
  opacity: 0;
  width: 1px;
  height: 1px;
  border-radius: 50%;
  transition: all 250ms ease-out;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.wentilab-floating-chat .chat.enter {
  opacity: 1;
  border-radius: 0;
  width: auto;
  height: auto;
}

.wentilab-floating-chat .chat.enter .chat-header {
  display: flex;
  align-items: center;
  height: 53px;
  background: #004ebc;
  padding: 12px 16px;
  border-top-left-radius: 12px;
  border-top-right-radius: 16px;
}

.wentilab-floating-chat .chat.enter .chat-header .header-title {
  flex-grow: 1;
  flex-shrink: 1;
  font-size: 18px;
  line-height: 28.84px;
}

.wentilab-floating-chat .chat.enter .chat-header svg {
  font-size: 15px;
}

.wentilab-floating-chat .chat.enter .chat-content {
  padding: 10px;
  margin: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  flex-grow: 1;
  background: transparent;
}

.wentilab-floating-chat .chat.enter .chat-content .chat-message-wrapper {
  width: 100%;
  align-items: end;
  justify-content: end;
  margin-bottom: 20px;
  padding-right: 5px;
}

.wentilab-floating-chat .chat.enter .chat-content .chat-message-wrapper-bot {
  width: 100%;
  align-items: start;
  margin-bottom: 20px;
  padding-right: 5px;
}

.wentilab-floating-chat .chat.enter .chat-content .chat-message-wrapper .chat-message-me {
  min-width: 80px;
  background-color: #004bec;
  color: white;
  padding: 12px 16px;
  border-radius: 20px 20px 5px 20px;
  font-size: 16px;
  line-height: 19px;
  max-width: 250px;
}

.wentilab-floating-chat .chat.enter .chat-content .chat-message-wrapper-bot .chat-message-bot {
  min-width: 80px;
  background-color: #f0f0f0;
  color: #1c1c1c;
  padding: 12px 16px;
  border-radius: 20px 20px 20px 5px;
  font-size: 16px;
  line-height: 19px;
  max-width: 250px;
}

.wentilab-floating-chat .chat.enter .chat-content .chat-message-wrapper-bot .chat-message-link-wrapper {
  background-color: #f0f0f0;
  padding: 12px;
  padding-bottom: 8px;
  border-radius: 15px;
  max-width: 250px;
}

.wentilab-floating-chat
  .chat.enter
  .chat-content
  .chat-message-wrapper-bot
  .chat-message-link-wrapper
  .chat-message-product-link {
  width: 100%;
  text-align: center;
  display: block;
  margin-top: 8px;
  color: #1c1c1c;
  font-size: 16px;
  line-height: 19px;
}

.wentilab-floating-chat .chat.enter .chat-footer {
  width: 100%;
  margin-top: 10px;
  color: black;
  padding: 10px;
  padding-bottom: 5px;
}

.wentilab-floating-chat .chat.enter .chat-footer .message-template-wrapper {
  width: 100%;
  justify-content: end;
  margin-bottom: 16px;
  cursor: pointer;
  gap: 10px;
  overflow-x: auto;
}

.wentilab-floating-chat .chat.enter .chat-footer .message-template-wrapper .message-template-item {
  background-color: #004ebc;
  color: white;
  padding: 10px 20px;
  border-radius: 15px;
  font-size: 14px;
}

.wentilab-floating-chat .chat.enter .chat-footer .chat-input-group {
  width: 100%;
  background-color: #fafafa;
  height: 48px;
  border-radius: 45px;
  box-shadow: 0px 2px 4px 0px #0000001a;
  border: 1px solid #e6e6e6;
}

.wentilab-floating-chat .chat.enter .chat-footer .chat-input-group .chat-input {
  color: #1c1c1c;
  background: transparent;
  height: 48px;
  border-radius: 30px;
  border: none;
  margin-right: 16px;
  margin-left: 4px;
}

.wentilab-floating-chat .chat.enter .chat-footer .chat-input-group .chat-input::placeholder {
  color: #1c1c1c;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  opacity: 30%;
}

.wentilab-floating-chat .chat.enter .chat-footer .chat-input-group .chat-input-right-element {
  height: 48px;
  margin-right: 16px;
}

.wentilab-floating-chat .chat.enter .chat-footer .chat-powered-by {
  width: 100%;
  text-align: center;
  font-size: 9px;
  margin-top: 5px;
  line-height: 9px;
}

.wentilab-floating-chat .chat.enter .chat-footer .chat-powered-by .link {
  text-decoration: underline;
  color: #5e50a0;
  font-size: 10px;
}
